.HighscoreWrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  vertical-align: middle;
  align-items: center;
  width: 95%;
  font-size: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(3.2px);
  -webkit-backdrop-filter: blur(3.2px);
  border: 1px solid rgba(255, 255, 255, 0.5);
  box-sizing: border-box;
  padding: 1rem 0rem;
}
.TitleWrapper {
  position: relative;
  text-align: center;
  font-size: 40px;
  color: #ffdc7f;
  width: 100%;
  margin-bottom: 1rem;
}
.Cup {
  position: absolute;
  top: -80%;
  left: 5%;
}
.CupImage {
}
.Player {
  color: #fff;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  box-shadow: 0 4px 30px rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(3.2px);
  -webkit-backdrop-filter: blur(3.2px);
  border: 1px solid rgba(255, 255, 255, 0.5);
  padding: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  vertical-align: middle;
  margin-top: 0.5rem;
  width: 80%;
}

.Rank {
  width: 10%;
}
.Name {
  width: 70%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding-right: 5%;
}
.Score {
  width: 15%;
  font-size: 28px;
  align-self: flex-end;
}
